
path {
  @apply tw-stroke-gray-300;
  stroke-width: 1px;
}

.rating-stars {
  position: relative;
  display: inline-flex;
}

.rating-stars__mask {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

.stars {
  display: flex;
  flex-wrap: nowrap;
}

.stars {
  color: rgb(243, 244, 246);
}

.star {
  width: 1rem;
  height: 1rem;
}

.stars--active {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  clip-path: v-bind(clipPathStyle);
  color: #facc15;

  path {
    stroke: #eab308;
  }
}

svg {
  fill: currentColor;
}

@media (prefers-color-scheme: dark) {
  path {
    stroke: theme('colors.gray.500');
  }

  .stars--active {
    path {
      stroke: theme('colors.yellow.500');
    }
  }
}
